@import url(https://fonts.googleapis.com/css?family=Roboto:100,300,400,500&display=swap);
body{margin:0;font-family:'Roboto', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.fw-lighter{font-weight:lighter}.shadow{-webkit-filter:drop-shadow(2px 2px 1px rgba(0,0,0,0.5));filter:drop-shadow(2px 2px 1px rgba(0,0,0,0.5))}@media (min-width: 767px){body{font-size:1.2em}}@media (min-width: 1200px){body{font-size:1.8em}}

.app{position:relative}.app .container{position:absolute;top:0;left:0;min-height:100%;min-width:100%;max-width:100vw}

.icon-rate{height:1em;margin-right:.2em;vertical-align:middle}

.exchange-article{display:flex;flex-flow:row wrap;justify-content:flex-start;align-items:center;margin-bottom:1em}.exchange-article>*{flex:0 1}.exchange-article__title,.exchange-article__rate{font-size:2.8em;margin:0;-webkit-filter:drop-shadow(1px 1px 1px rgba(0,0,0,0.3));filter:drop-shadow(1px 1px 1px rgba(0,0,0,0.3));flex:0 1 auto}.exchange-article__title:not(:last-child),.exchange-article__rate:not(:last-child){margin-right:.5em}.exchange-article__title{font-weight:bold;color:#ffffff}@media not all and (min-width: 767px){.exchange-article__title{flex:1 1 100%}}.exchange-article__rate{font-weight:normal}@media not all and (min-width: 767px){.exchange-article__rate{flex:0 1}.exchange-article__rate .icon-rate{display:none}}.exchange-article__rate .rate-item{display:inline-block}.exchange-article__chart{flex:0 1 auto}

.rate-item{color:#f7c73f}.rate-item__up{color:#057389}.rate-item__down{color:#d29681}

header{color:#f1f1f1;padding:1em;display:flex;flex-flow:row wrap;justify-content:flex-start;align-items:center}header>*{flex:0 1}header .logo{display:block;height:3.4em;margin-right:.2em;pointer-events:none;-webkit-animation:logo-spin infinite 20s linear;animation:logo-spin infinite 20s linear;align-self:flex-end;flex-basis:4em}header .brand{background:linear-gradient(0.1turn, #057389 0%, #f7c73f 20%, #f7c73f 20%);background-size:200% auto;color:transparent;font-weight:normal;font-size:2.8em;-webkit-background-clip:text;background-clip:text;-webkit-animation:bg-shine 3s linear infinite;animation:bg-shine 3s linear infinite;margin:0;align-self:flex-start}header .slogan{flex:1 1 50%;margin:0;color:#f7c73f;font-weight:lighter;align-self:flex-end}header .dt{flex:1 1 50%;display:flex;flex-flow:row;justify-content:space-between;align-items:center;opacity:.5}@media (min-width: 767px){header .dt{order:9;margin-left:auto;color:#ffffff;flex-flow:column wrap;align-items:flex-end}header .slogan{order:10}}@-webkit-keyframes bg-shine{to{background-position:200% center}}@keyframes bg-shine{to{background-position:200% center}}@-webkit-keyframes logo-spin{0%{transform:rotate(0deg)}25%{transform:rotate(180deg)}50%{transform:rotate(180deg)}75%{transform:rotate(360deg)}100%{transform:rotate(360deg)}}@keyframes logo-spin{0%{transform:rotate(0deg)}25%{transform:rotate(180deg)}50%{transform:rotate(180deg)}75%{transform:rotate(360deg)}100%{transform:rotate(360deg)}}


.background{position:fixed;top:-8px;left:-8px;min-width:calc(100% + 4px * 4);min-height:calc(100% + 4px * 4)}.background_error{-webkit-filter:blur(4px) sepia(1);filter:blur(4px) sepia(1)}.background_loading{-webkit-filter:blur(4px) grayscale(1);filter:blur(4px) grayscale(1)}.background_ready{-webkit-filter:blur(4px) brightness(0.7);filter:blur(4px) brightness(0.7)}

main{padding:21.44px}

